$Fonts: ('Montserrat', 'Regular', 400), 
        ('Montserrat', 'Bold', 700)
        ;

@each $family, $name, $weight in $Fonts {
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}-#{$name}.ttf);
        font-weight: $weight;
    }    
}

$white: #ffffff;
$black: #000000;
$main: #3d64af;